.tweet {  border: 1px solid #ccc;  width: 564px;  min-height: 68px;  padding: 10px;  display: -ms-flexbox;  display: flex;  font-family: "Helvetica", arial, sans-serif;  font-size: 14px;  line-height: 18px;}.avatar {  width: 48px;  height: 48px;  border-radius: 5px;  margin-right: 10px;}.name {  font-weight: bold;  margin-bottom: 0.5 em;  margin-right: 0.3em;}.handle {  color: #8899a6;  font-size: 13px;}.time {  padding-left: 0.3em;  color: #8899a6;}.time::before {  content: "\00b07";  padding-right: 0.3em;}.buttons {  margin-top: 10px;  margin-left: 2px;  font-size: 1.4em;  color: #aab8c2;}.buttons i {  width: 100px;}.reply-button, .retweet-button,.like-button, .more-options-button {  width: 80px;  display: inline-block;}.like-count, .retweet-count {  position: relative;  bottom: 2px;  font-size: 15px;  margin-left: 5px;  font-weight: bold;}


